import { firebase, FieldValue } from "../lib/firebase";
export async function doesUsernameExist(username) {
  const result = await firebase
    .firestore()
    .collection("users")
    .where("username", "==", username.toLowerCase())
    .get();

  return result.docs.length > 0;
}

export async function getUserByUsername(username) {
  const result = await firebase
    .firestore()
    .collection("users")
    .where("username", "==", username.toLowerCase())
    .get();

  return result.docs.map((item) => ({
    ...item.data(),
    docId: item.id,
  }));
}

// get user from the firestore where userId === userId (passed from the auth)
export async function getUseobjbyId(userId) {
  const result = await firebase
    .firestore()
    .collection("users")
    .where("userId", "==", userId)
    .get();
  const user = result.docs.map((item) => ({
    ...item.data(),
    docId: item.id,
  }));

  return user;
}

export async function getsuggetionProfile(userId, following) {
  const result = await firebase.firestore().collection("users").limit(10).get();

  return result.docs
    .map((user) => ({
      ...user.data(),
      docId: user.id,
    }))
    .filter(
      (profile) =>
        profile.userId != userId && !following.includes(profile.userId)
    );
}

export async function updateloginusflw(maindocId, profileId, isFollowing) {
  await firebase
    .firestore()
    .collection("users")
    .doc(maindocId)
    .update({
      following: isFollowing
        ? FieldValue.arrayRemove(profileId)
        : FieldValue.arrayUnion(profileId),
    });
}

export async function updateflwruser(userId, userDocId, isFollowing) {
  await firebase
    .firestore()
    .collection("users")
    .doc(userDocId)
    .update({
      followers: isFollowing
        ? FieldValue.arrayRemove(userId)
        : FieldValue.arrayUnion(userId),
    });
}

export async function getphoto(userId, following) {
  const result = await firebase
    .firestore()
    .collection("photos")
    .where("userId", "in", following)
    .get();
  const userFollowingPhotos = result.docs.map((photo) => ({
    ...photo.data(),
    docId: photo.id,
  }));

  const photoWithuserDetails = await Promise.all(
    userFollowingPhotos.map(async (photo) => {
      let userLikedphoto = false;
      if (photo.likes.includes(userId)) {
        userLikedphoto = true;
      }

      const user = await getUseobjbyId(photo.userId);

      const { username } = user[0];

      return { username, ...photo, userLikedphoto };
    })
  );

  return photoWithuserDetails;
}

export async function getuserPhotosByUsername(username) {
  const [user] = await getUserByUsername(username);
  const result = await firebase
    .firestore()
    .collection("photos")
    .where("userId", "==", user.userId)
    .get();
  return result.docs.map((item) => ({
    ...item.data(),
    docId: item.id,
  }));
}

