import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { doesUsernameExist } from "../services/firebase";
import React from "react";
import { useNavigate } from "react-router-dom";
import * as ROUTS from "../constants/routs";
import { firebase} from "../lib/firebase";
import {
  getAuth,
  createUserWithEmailAndPassword,
  updateProfile,
} from "firebase/auth";
export default function signup() {
  const [email, setEmail] = useState("");
  const [username, setName] = useState("");
  const [Phone, setPhone] = useState("");
  const [Password, setPassword] = useState("");
  const [error, seterror] = useState("");
  //const firebase=useContext(FirebaseContext);
  const onhistory = useNavigate();
  const Invalid =
    email == "" || Password == "" || Phone == "" || username == "";
  //console.log(firebase.);
  const handlelogin = async (event) => {
    event.preventDefault();
    const userexist = await doesUsernameExist(username);
    if (!userexist.length) {
      try {
        const auth = getAuth();
        const createduserresult = await createUserWithEmailAndPassword(
          auth,
          email,
          Password
        );

        updateProfile(auth.currentUser, {
          displayName: username,
        });
        await firebase
          .firestore()
          .collection("users")
          .add({
            displayName: username,
            userId: createduserresult.user.uid,
            username: username.toLowerCase(),
            fullName: username,
            emailAddress: email.toLowerCase(),
            following: ["2"],
            dateCreated: Date.now(),
          });

        onhistory(ROUTS.DASHBOARD);
      } catch (error) {
        console.log(2);
        setEmail("");
        setPassword("");
        setName("");
        setPhone("");
        seterror(error.message);
      }
    } else {
      seterror("that user name is already exist ,please try anthore");
    }
  };

  useEffect(() => {
    document.title = "Sign up-Instgrame";
  }, []);

  return (
    <div className="container flex mx-auto max-w-screen-md items-center h-screen ">
      <div className="flex flex-col  sm:w-2/5 w-10/12 m-auto">
        <div className="flex flex-col bg-white justify-center items-center border border-gray-primary mb-4 p-4">
          <h1 className="flex justify-center w-full">
            <img
              src="/images/logo.png"
              alt="logo"
              className="mt-2 w-6/12 mb-4"
            />
          </h1>
          <h2 className="font-bold p-2 text-base text-center h-3/4 text-gray-base">
            Sign up to see photos and videos from your friends.
          </h2>
          {error && <p className="mb-4 text-xs text-red-primary">{error}</p>}
          <form onSubmit={handlelogin} method="POST">
            <input
              type=""
              aria-label="Enter your Phone number"
              placeholder="enter your phone number"
              className="text-sm text-gray-base py-5 px-4 mr-3 border w-full h-2 border-gray-primary mb-2 rounded"
              onChange={({ target }) => setPhone(target.value)}
              value={Phone}
            />
            <input
              type="text"
              aria-label="Enter your name"
              placeholder="enter your name"
              className="text-sm text-gray-base py-5 px-4 mr-3 border w-full h-2 border-gray-primary mb-2 rounded"
              onChange={({ target }) => setName(target.value)}
              value={username}
            />

            <input
              type="text"
              aria-label="Enter your Email"
              placeholder="enter email"
              className="text-sm text-gray-base py-5 px-4 mr-3 border w-full h-2 border-gray-primary mb-2 rounded"
              onChange={({ target }) => setEmail(target.value)}
              value={email}
            />

            <input
              aria-label="Enter your password"
              type="password"
              value={Password}
              placeholder="enter password"
              className="text-sm text-gray-base py-5 px-4 border mr-3  w-full h-2 border-gray-primary mb-2 rounded "
              onChange={({ target }) => setPassword(target.value)}
            />
            <button
              disabled={Invalid}
              className={`bg-blue-medium text-white rounded w-full h-8 font-bold ${
                Invalid && "opacity-50 "
              }`}
            >
              Log In
            </button>
          </form>
        </div>
        <div className="flex justify-center items-center flex-col w-full bg-white rounded p-4 border border-gray-primary">
          <p className="text-sm ">
            {" "}
            do you have acount?{" "}
            <Link to="/Login" className="font-blod text-blue-medium">
              Log In
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
